.DDElement {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 10px;
  gap: 5px;
  font-size: 14px;
}

.DDElement:hover {
  cursor: pointer;
}

.DDText {
  font-weight: normal;
  font-size: 1em;
}

.DDText:hover {
  color: var(--Ablack);
  /* font-weight: 700; */
}

.DDIcon {
  height: 6px;
  width: 10px;
  text-align: center;
}

.DDCollapse {
  display: none;
}

.expandedItemsDiv {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: flex-start;
  align-self: flex-start;
  margin: 5px 0px;
  margin-left: 50px;
  padding: 10px;
}

@media screen and (min-width: 750px) {
  .desktopDD {
    position: relative;
  }
  .expandedItemsDiv {
    margin: 0px;
    position: absolute;
    top: 100%;
    left: 0%;
    border: 1px solid white;
    background-color: white;
    box-shadow: 0px 0px 10px -1px grey;
    padding: 10px;
    border-radius: 10px;
  }
}
