.heroSection {
  display: flex;
  padding: 50px;
  padding-top: 100px;
  /* height: 80vh; */
  /* gap: 10px; */
}

.heroImageDiv {
  display: flex;
  width: 50%;
  /* align-items: center; */
  justify-content: center;
  /* padding: 20px; */
  margin-left: auto;
}

.heroImg {
  content: url("../../images/heroDesktop.png");
  /* content: url("/public/assets/images/heroDesktop.png"); */
  width: 90%;
  height: 70%;
  transition: width 1s ease-in-out;
}
.heroTextDiv {
  width: 40%;
  padding: 20px;
  padding-left: 40px;
  padding-right: 0px;
}

.herotitle {
  font-size: 3rem;
  color: var(--Ablack);
  padding: 20px;
  padding-left: 0px;
  padding-right: 0px;
}
.subtitle {
  font-size: 0.8rem;
  padding: 20px 0px;
}
.heroButton {
  /* margin: 20px 0px 10px; */
  padding: 10px 20px;
  color: var(--Awhite);
  background-color: var(--Ablack);
  border: 0px;
  border-radius: 10px;
}
.heroButtonDiv {
  margin: 20px 0px 10px;
  height: 50px;
}

.heroButton:hover {
  color: var(--Ablack);
  background-color: var(--Awhite);
  border: 1px solid var(--Ablack);
  font-weight: 700;
  cursor: pointer;
}

.herolinks {
  margin-top: 20px;
  display: flex;
  gap: 10px;
  align-items: center;
  flex-wrap: wrap;
}
.linkImg {
  width: 20%;
}

@media screen and (max-width: 750px) {
  .heroImg {
    /* content: url("/public/assets/images/image-hero-mobile.png"); */
    content: url("../../images/image-hero-mobile.png");
  }
  .heroSection {
    flex-direction: column;
  }
  .heroImageDiv {
    width: 100%;
    order: 1;
  }
  .heroTextDiv {
    width: 100%;
    order: 2;
    padding-left: 0px;
  }
  .herotitle {
    text-align: center;
  }
  .subtitle {
    text-align: center;
  }
  .heroButtonDiv {
    display: flex;
    justify-content: center;
    padding-bottom: 10px;
  }
  .herolinks {
    justify-content: center;
  }
}

@media screen and (min-width: 1100px) {
  .heroImg {
    width: 80%;
    transition: width 0.5s ease-in-out;
  }
}
@media screen and (min-width: 1300px) {
  .heroImg {
    width: 70%;
    transition: width 0.5s ease-in-out;
  }
}
@media screen and (min-width: 1600px) {
  .heroImg {
    width: 60%;
    transition: width 0.5s ease-in-out;
  }
}
